@media screen and (max-width: 1400px), screen and (max-device-width: 1400px) {
  body {
    font-size: 16px;
    line-height: 18px;
    .content {
      .container {
        grid-template-columns: 25% 1fr 35%;
        .content__productCard__descr{
          margin-top: 85px;
          .packaging,
          .plate{
            button{
              width: 105px;
            }
          }
          .consultation{
            a{
              width: 250px;
            }
          }
        }
        .content__recomend{
          .slider {
            height: 290px;
            .lSSlideOuter {
              ul {
                .item-a,
                .item-b,
                .item-c,
                .item-e,
                .item-d,
                .item-f {
                  width: 235px;
                  height: 290px;
                }
                li {
                  img {
                    width: 150px;
                    height: 150px;
                  }
                }
              }
            }
          }
        }
        .content__howToMakeOrder {
          div {
            .info {
              margin-left: 55px;
            }
          }
        }
      }
    }
    .contentAddres {
      iframe {
        height: 465px;
      }
      .contentAddres__contactForm {
        .comentary {
          input[type*="text"] {
            width: 184px;
          }
          textarea {
            width: 395px;
          }
        }
        .contacts {
          width: 75%;
        }
      }
    }
  }
}
